<template>
  <div id="black_model">
    <!-- title -->
    <div class="top_title">
      黑名单内人员无法在哎哟有房小程序订房或办理入住，并会提示“系统错误，无法进行下一步”。
    </div>
    <!-- 添加黑名单 -->
    <van-button class="add_style"
                @click="show_addPerson = true">+ 新增黑名单</van-button>
    <!-- 搜索 -->
    <div style="display:flex;justify-content:center;">
      <input type="search"
             results="5"
             name="word"
             id="Search"
             placeholder="请输入手机号或身份证号查询"
             v-model="searchValue"
             @keyup.enter="searchAnimation"
             class="input_border" />
      <div class="search"
           @click="Search()">搜索</div>
    </div>
    <!-- 列表 -->
    <div v-if="blackList.length > 0"
         class="scroll">
      <van-pull-refresh v-model="pullLoading"
                        @refresh="onPullRefresh"
                        success-text="刷新成功">
        <van-list v-model="listLoading"
                  @load="onLoadList"
                  :offset="30"
                  :finished="finishedList"
                  finished-text="没有更多了">
          <div v-for="(item, i) in blackList"
               :key="i"
               class="content">
            <div class="hotel-left">
              <div>
                <div class="c_000">
                  <div class="aaa">手机号</div>
                  <div :style="{color:item.phone?'#000':''}">{{ item.phone || '暂无' }}</div>
                </div>
                <div class="c_000">
                  <div class="aaa">身份证</div>
                  <div :style="{color:item.cardNo?'#000':''}">{{ item.cardNo || '暂无' }}</div>
                </div>
                <div class="c_000">
                  <div class="aaa">添加时间</div>
                  <div>{{ item.createTime || '暂无' }}&ensp;{{ item.realName }} </div>
                </div>
              </div>
              <div style="color:red;"
                   @click="remove(item)">移出</div>
            </div>
          </div>
        </van-list>
      </van-pull-refresh>
    </div>
    <div v-else
         class="nothing">
      <img src="@/assets/hotelAudit/empty.png"
           alt="">
    </div>
    <!-- 展示-新增人员 -->
    <van-dialog v-model="show_addPerson"
                :before-close="confirmDepart"
                show-cancel-button>
      <div>
        <div class="add_top">
          <div>新增人员</div>
          <div style="color:#cecece;font-size:0.7rem;">(至少输入一项)</div>
        </div>
        <van-cell-group>
          <van-field v-model="dataAdd.phone"
                     label="手机号"
                     type="tel"
                     :maxlength='11'
                     placeholder="请输入手机号" />
          <van-field v-model="dataAdd.cardNo"
                     label="身份证号"
                     :maxlength='18'
                     placeholder="请输入身份证号" />
        </van-cell-group>
      </div>
    </van-dialog>
  </div>
</template>
<script>
import { Toast, Dialog } from 'vant';
import { GetBusinessBlackUserList, PostBusinessBlackUserAdd, PostBusinessBlackUserRemove } from '@/api/api_black';
export default {
    data() {
        return {
            blackList: [],
            searchValue: null,
            show_addPerson: false,
            dataAdd: {
                hotelId: this.$route.query.hotelId,
                phone: null,
                cardNo: null
            },
            dataForm: {
                pageNum: 0,
                pageSize: 10,
                hotelId: this.$route.query.hotelId,
                searchValue: null
            }
        };
    },
    created() {
        this.$store.commit('setHeader', '客户黑名单');
        this.onLoadList();
    },
    methods: {
        // 回车
        searchAnimation() {
            this.dataForm.searchValue = this.searchValue;
            this.blackList = [];
            this.dataForm.pageNum = 0;
            this.onLoadList();
        },
        // 搜索
        Search() {
            this.dataForm.searchValue = this.searchValue;
            this.blackList = [];
            this.dataForm.pageNum = 0;
            this.onLoadList();
        },
        //  下拉刷新列表
        onPullRefresh() {
            this.blackList = [];
            this.dataForm.pageNum = 0;
            this.finishedList = false;
            this.onLoadList();
        },
        //  上滑加载列表
        onLoadList() {
            GetBusinessBlackUserList(this.dataForm).done((res) => {
                this.blackList = this.blackList.concat(res.data);
                this.listLoading = this.pullLoading = false;
                if (res.total == this.blackList.length) {
                    this.finishedList = true;
                } else if (res.total > this.blackList.length) {
                    this.finishedList = false;
                }
            });
            this.dataForm.pageNum++;
        },
        // 生活费用-确定
        confirmDepart(action, done) {
            if (action == 'confirm') {
                done(false);
                if (!this.dataAdd.phone && !this.dataAdd.cardNo) {
                    Toast('请至少输入一项');
                    return;
                }
                // 已验证191,181手机号
                if (this.dataAdd.phone && !/^((13[0-9])|(14[5-9])|(15([0-3]|[5-9]))|(16[6-7])|(17[1-8])|(18[0-9])|(19[1|3])|(19[5|6])|(19[8|9]))\d{8}$/.test(this.dataAdd.phone)) {
                    Toast('手机号码有误');
                    this.dataAdd.phone = '';
                    return;
                }
                if (this.dataAdd.phone && !/^[1][3,4,5,7,8,9][0-9]{9}$/.test(this.dataAdd.phone)) {
                    Toast('手机号码有误');
                    this.dataAdd.phone = '';
                    return;
                }
                PostBusinessBlackUserAdd(this.dataAdd).done((res) => {
                    Toast(res.msg);
                    this.dataAdd.phone = null;
                    this.dataAdd.cardNo = null;
                    this.blackList = [];
                    this.dataForm.pageNum = 0;
                    this.onLoadList();
                });
                this.show_addPerson = false;
            } else if (action == 'cancel') {
                done(false);
                this.show_addPerson = false;
            }
        },
        // 移出
        remove(item) {
            Dialog.confirm({
                title: '确定移除'
            })
                .then(() => {
                    PostBusinessBlackUserRemove({ blackUserId: item.id }).done((res) => {
                        this.blackList = [];
                        this.dataForm.pageNum = 0;
                        this.onLoadList();
                    });
                })
                .catch(() => {});
        }
    }
};
</script>
<style scoped>
#black_model {
    font-size: 0.8rem;
}
.top_title {
    text-align: left;
    padding: 10px;
    background: #f5c4cc;
    color: #3d3832;
}
.add_style {
    width: 100%;
    margin: 8px 0 0 0;
    font-weight: bold;
}
input {
    -webkit-appearance: none !important;
    text-indent: 2em;
    margin: 10px 0;
    width: 82%;
    height: 32px;
    border: 0;
    outline: none;
    box-sizing: border-box;
}
#Search::-webkit-search-cancel-button {
    display: none;
}
.input_border {
    font-size: 0.7rem;
}
.search {
    border: none;
    font-size: 0.7rem;
    color: #fff;
    margin-top: 10px;
    display: inline-block;
    width: 15%;
    height: 32px;
    line-height: 32px;
    background-color: #ff7d41;
}
.search:active {
    opacity: 0.5;
}
.add_top {
    margin: 20px 0;
    font-size: 0.8rem;
    line-height: 20px;
}
.content {
    padding: 10px 20px;
    color: #a6a3a6;
    font-size: 0.7rem;
    background: #fff;
    border-bottom: 1px solid #e5e2e6;
}
.hotel-left {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.scroll {
    position: fixed;
    left: 0;
    right: 0;
    top: 211px;
    overflow-y: scroll;
    bottom: 0;
}
.c_000 {
    display: flex;
    align-items: flex-end;
    font-size: 0.7rem;
    line-height: 22px;
}
.aaa {
    width: 60px;
    text-align: left;
    padding-right: 15px;
}
.nothing {
    margin-top: 50px;
}
/deep/ .van-field__label {
    margin: 0;
}
/deep/ .van-field__control {
    background: #f1eff2;
    text-indent: 1rem;
}
</style>



