import * as API from './';
import {
  data
} from 'autoprefixer';
/*********************************************  财务模块  *******************************************/
// 分页查询黑名单
export const GetBusinessBlackUserList = data => API.GET(`/api/business/black/user/list`, data, true)
// 添加黑名单
export const PostBusinessBlackUserAdd = data => API.POST(`/api/business/black/user/add`, data, true)
// 删除黑名单
export const PostBusinessBlackUserRemove = data => API.POST(`/api/business/black/user/remove/${data.blackUserId}`, data, true)



